    <input v-model="password" type="password" class="w-96 px-4 py-3 rounded border border-gray-300 focus:ring-2 focus:border-primary" placeholder="ë¹„ë°€ë²ˆí˜¸">
    <button @click ="onLogin" class="w-96 rounded bg-primary text-white py-4 hover:bg-dark">ë¡œê·¸ì¸</button>
    <router-link to="/register">
